/* Game.css */

.game {
    text-align: center;
    padding: 1.5rem; /* Reduced padding */
  }
  
  .game h1 {
    font-size: 2rem; /* Reduced font size */
    margin-bottom: 1.5rem; /* Reduced margin */
  }
  
  .guess-buttons {
    margin: 1rem 0; /* Adjusted margin to provide space around the buttons */
  }
  
  .guess-buttons h3 {
    font-size: 1.4rem; /* Increased heading size slightly */
    margin-bottom: 0.75rem;
  }
  
  .guess-buttons button {
    background-color: #1976d2;
    color: #fff;
    border: none;
    padding: 0.75rem 1.5rem; /* Increased padding for larger buttons */
    margin: 0 0.5rem; /* Increased margin between buttons */
    font-size: 1rem; /* Increased font size for better readability */
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease, transform 0.2s; /* Added transform for hover effect */
  }
  
  .guess-buttons button:hover {
    background-color: #1565c0;
    transform: scale(1.05); /* Slightly enlarge button on hover */
  }
  
  .guess-buttons button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .loading {
    font-size: 1rem; /* Reduced font size */
    margin: 0.5rem 0; /* Reduced margin */
  }
  
  .results-history {
    margin-top: 1.5rem; /* Reduced margin */
  }
  
  .results-history h3 {
    font-size: 1.3rem; /* Slightly smaller heading */
    margin-bottom: 0.75rem; /* Reduced margin */
  }
  
  .results-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .result-entry {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 0.75rem; /* Reduced margin */
    font-size: 1rem; /* Reduced font size */
    border-bottom: 1px solid #ddd;
    padding-bottom: 0.25rem; /* Reduced padding */
    width: 100%;
    max-width: 600px;
  }
  
  .result-entry .emoji {
    font-size: 1.5rem; /* Reduced emoji size */
    margin: 0 0.25rem; /* Reduced margin */
  }
  
  .result-entry .label {
    margin: 0 0.25rem;
    font-weight: bold;
  }
  
  .result-entry .guess {
    margin: 0 0.25rem;
    color: #555;
  }
  
  .result-entry .vs {
    margin: 0 0.5rem;
    font-weight: bold;
  }
  
  .result-entry .confidence {
    margin-left: auto;
    font-size: 0.8rem; /* Reduced font size */
    color: #777;
    text-align: right;
    flex-basis: 100%;
    margin-top: 0.25rem; /* Reduced margin */
  }
  
  .scoreboard {
    margin-top: 1.5rem; /* Reduced margin */
  }
  
  .scoreboard h3 {
    font-size: 1.3rem; /* Slightly smaller heading */
    margin-bottom: 0.75rem; /* Reduced margin */
  }
  
  .scoreboard p {
    font-size: 1rem; /* Reduced font size */
    margin: 0.25rem 0; /* Added margin between paragraphs */
  }
  
  /* Shake animation remains unchanged */
  @keyframes shake {
    0% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    50% { transform: translateX(5px); }
    75% { transform: translateX(-5px); }
    100% { transform: translateX(0); }
  }
  
  .shake {
    animation: shake 0.5s;
    display: inline-block;
  }
  